import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { Music, Search, Grid, List, Heart, Share2, Trash2, Plus, Play } from 'lucide-react';

const MyPlaylists = () => {
  const [playlists, setPlaylists] = useState([]);
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState('');
  const [sortBy, setSortBy] = useState('recent'); // recent, name, tracks, likes
  const [viewMode, setViewMode] = useState('grid'); // grid, list
  const [filterBy, setFilterBy] = useState('all'); // all, public, private
  
  // Mock data for demo
  const mockPlaylists = [
    {
      _id: '1',
      name: 'Happy Morning Vibes',
      description: 'Start your day with positive energy',
      tracks: [
        { name: 'Good as Hell', artist: 'Lizzo' },
        { name: 'Uptown Funk', artist: 'Mark Ronson ft. Bruno Mars' },
        { name: 'Happy', artist: 'Pharrell Williams' }
      ],
      isPublic: true,
      likes: 24,
      createdAt: '2024-01-15',
      moodPrompt: 'feeling happy and energetic'
    },
    {
      _id: '2', 
      name: 'Study Focus',
      description: 'Concentration tracks for productivity',
      tracks: [
        { name: 'Weightless', artist: 'Marconi Union' },
        { name: 'Clair de Lune', artist: 'Claude Debussy' }
      ],
      isPublic: false,
      likes: 12,
      createdAt: '2024-01-12',
      moodPrompt: 'need focus for studying'
    },
    {
      _id: '3',
      name: 'Workout Pump',
      description: 'High-energy tracks for your workout',
      tracks: [
        { name: 'Till I Collapse', artist: 'Eminem' },
        { name: 'Stronger', artist: 'Kanye West' },
        { name: 'Eye of the Tiger', artist: 'Survivor' }
      ],
      isPublic: true,
      likes: 45,
      createdAt: '2024-01-10',
      moodPrompt: 'workout motivation'
    }
  ];

  useEffect(() => {
    // Simulate API call
    setTimeout(() => {
      setPlaylists(mockPlaylists);
      setLoading(false);
    }, 1000);
  }, []);

  const deletePlaylist = async (playlistId) => {
    if (!window.confirm('Are you sure you want to delete this playlist?')) return;
    
    setPlaylists(playlists.filter(p => p._id !== playlistId));
  };

  const togglePublic = async (playlistId, currentStatus) => {
    setPlaylists(playlists.map(p => 
      p._id === playlistId 
        ? { ...p, isPublic: !currentStatus }
        : p
    ));
  };

  const filteredAndSortedPlaylists = playlists
    .filter(playlist => {
      const matchesSearch = playlist.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
                           playlist.description.toLowerCase().includes(searchTerm.toLowerCase());
      const matchesFilter = filterBy === 'all' ||
                           (filterBy === 'public' && playlist.isPublic) ||
                           (filterBy === 'private' && !playlist.isPublic);
      return matchesSearch && matchesFilter;
    })
    .sort((a, b) => {
      switch (sortBy) {
        case 'name':
          return a.name.localeCompare(b.name);
        case 'tracks':
          return b.tracks.length - a.tracks.length;
        case 'likes':
          return b.likes - a.likes;
        case 'recent':
        default:
          return new Date(b.createdAt) - new Date(a.createdAt);
      }
    });

  if (loading) {
    return (
      <div className="container">
        <div className="loading">
          <div className="spinner"></div>
          <p>Loading your playlists...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="container">
      {/* Header */}
      <div className="header">
        <h1><Music size={32} style={{marginRight: '12px'}} />My Playlists</h1>
        <p>Manage and organize your AI-generated music collections</p>
        
        <Link 
          to="/generate"
          className="btn btn-primary"
          style={{ 
            display: 'inline-flex', 
            alignItems: 'center', 
            gap: '0.5rem',
            fontSize: '1.1rem',
            padding: '18px 36px'
          }}
        >
          <Plus size={20} />
          Create New Playlist
        </Link>
      </div>

      {/* Controls */}
      <div className="glass-card" style={{marginBottom: '2rem'}}>
        <div className="flex flex-col md:flex-row" style={{gap: '1rem', alignItems: 'stretch'}}>
          {/* Search */}
          <div style={{flex: 1, position: 'relative'}}>
            <Search size={20} style={{
              position: 'absolute', 
              left: '15px', 
              top: '50%', 
              transform: 'translateY(-50%)',
              color: '#666'
            }} />
            <input
              type="text"
              className="form-input"
              style={{paddingLeft: '50px'}}
              placeholder="Search your playlists..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
            />
          </div>

          {/* Filters and Sort */}
          <div className="flex" style={{gap: '0.5rem'}}>
            <select
              value={filterBy}
              onChange={(e) => setFilterBy(e.target.value)}
              className="form-input"
              style={{minWidth: '120px'}}
            >
              <option value="all">All</option>
              <option value="public">Public</option>
              <option value="private">Private</option>
            </select>

            <select
              value={sortBy}
              onChange={(e) => setSortBy(e.target.value)}
              className="form-input"
              style={{minWidth: '140px'}}
            >
              <option value="recent">Most Recent</option>
              <option value="name">Name A-Z</option>
              <option value="tracks">Most Tracks</option>
              <option value="likes">Most Liked</option>
            </select>

            <div className="flex" style={{gap: '5px'}}>
              <button
                onClick={() => setViewMode('grid')}
                className={`btn ${viewMode === 'grid' ? 'btn-primary' : ''}`}
                style={{
                  padding: '12px',
                  background: viewMode === 'grid' ? undefined : 'rgba(255,255,255,0.2)'
                }}
              >
                <Grid size={16} />
              </button>
              <button
                onClick={() => setViewMode('list')}
                className={`btn ${viewMode === 'list' ? 'btn-primary' : ''}`}
                style={{
                  padding: '12px',
                  background: viewMode === 'list' ? undefined : 'rgba(255,255,255,0.2)'
                }}
              >
                <List size={16} />
              </button>
            </div>
          </div>
        </div>
      </div>

      {/* Results Info */}
      <div style={{marginBottom: '2rem', opacity: 0.8}}>
        <p>{filteredAndSortedPlaylists.length} playlist{filteredAndSortedPlaylists.length !== 1 ? 's' : ''} found</p>
      </div>

      {/* Playlists Display */}
      {filteredAndSortedPlaylists.length === 0 ? (
        <div className="glass-card text-center" style={{padding: '4rem 2rem'}}>
          <Music size={64} style={{margin: '0 auto 2rem', opacity: 0.5}} />
          <h3 style={{marginBottom: '1rem'}}>No playlists found</h3>
          <p style={{opacity: 0.8, marginBottom: '2rem'}}>
            {searchTerm ? 
              'Try adjusting your search or filters' : 
              "You haven't created any playlists yet"
            }
          </p>
          <Link to="/generate" className="btn btn-primary">
            <Plus size={20} style={{marginRight: '8px'}} />
            Create Your First Playlist
          </Link>
        </div>
      ) : (
        <div className={viewMode === 'grid' ? 'playlist-section' : ''} style={{gap: viewMode === 'grid' ? '20px' : '15px'}}>
          {filteredAndSortedPlaylists.map((playlist) => (
            <div key={playlist._id} className={viewMode === 'grid' ? 'playlist-card' : 'glass-card'}>
              <div className="playlist-header">
                <div className="playlist-icon">
                  <Music size={24} />
                </div>
                <div className="playlist-info" style={{flex: 1}}>
                  <h3>{playlist.name}</h3>
                  <p>{playlist.description}</p>
                  <p style={{fontSize: '0.8rem', marginTop: '5px'}}>
                    {playlist.tracks.length} tracks â€¢ Created {new Date(playlist.createdAt).toLocaleDateString()}
                  </p>
                </div>
                
                {/* Action Buttons */}
                <div className="flex" style={{gap: '8px', alignItems: 'flex-start'}}>
                  <button
                    onClick={() => togglePublic(playlist._id, playlist.isPublic)}
                    className="play-btn"
                    title={playlist.isPublic ? 'Make Private' : 'Make Public'}
                  >
                    <Share2 size={16} style={{color: playlist.isPublic ? '#4ecdc4' : '#fff'}} />
                  </button>
                  
                  <Link 
                    to={`/playlist/${playlist._id}`}
                    className="play-btn"
                    title="View Details"
                  >
                    <Play size={16} />
                  </Link>
                  
                  <button
                    onClick={() => deletePlaylist(playlist._id)}
                    className="play-btn"
                    title="Delete Playlist"
                    style={{background: 'rgba(255, 107, 107, 0.2)'}}
                  >
                    <Trash2 size={16} />
                  </button>
                </div>
              </div>

              {/* Track Preview */}
              <div className="track-list" style={{marginTop: '1rem'}}>
                {playlist.tracks.slice(0, 3).map((track, index) => (
                  <div key={index} className="track-item">
                    <div className="album-art" style={{width: '35px', height: '35px'}}>
                      <Music size={16} />
                    </div>
                    <div className="track-info">
                      <div className="track-name" style={{fontSize: '0.9rem'}}>{track.name}</div>
                      <div className="track-artist" style={{fontSize: '0.8rem'}}>{track.artist}</div>
                    </div>
                  </div>
                ))}
                
                {playlist.tracks.length > 3 && (
                  <p style={{fontSize: '0.8rem', opacity: 0.7, marginTop: '8px'}}>
                    +{playlist.tracks.length - 3} more tracks
                  </p>
                )}
              </div>

              {/* Stats */}
              <div className="flex justify-between" style={{marginTop: '1rem', paddingTop: '1rem', borderTop: '1px solid rgba(255,255,255,0.1)'}}>
                <div className="flex items-center" style={{gap: '4px'}}>
                  <Heart size={16} />
                  <span style={{fontSize: '0.8rem'}}>{playlist.likes}</span>
                </div>
                <div style={{fontSize: '0.8rem', opacity: 0.7}}>
                  {playlist.isPublic ? 'Public' : 'Private'}
                </div>
              </div>
            </div>
          ))}
        </div>
      )}
    </div>
  );
};

export default MyPlaylists;